import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import CSSTransitionGroup from 'react-addons-css-transition-group';

function Square(props) {
    const squares = props.squares;
    const highlight = 
        calculateWinner(squares).winner ?
            calculateWinner(squares).winningSquares.includes(props.index) ? 'highlighted' : '' :
            '';
    return (
        <button className={`square ${highlight}`} onClick={props.onClick}>
            {props.value}
        </button>
    );
}

function calculateWinner(squares) {
    const lines = [
        [0, 1, 2],
        [3, 4, 5],
        [6, 7, 8],
        [0, 3, 6],
        [1, 4, 7],
        [2, 5, 8],
        [0, 4, 8],
        [2, 4, 6],
    ];
    for (let i = 0; i < lines.length; i++) {
        const [a, b, c] = lines[i];
        if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
            return {
                winner: squares[a],
                winningSquares: lines[i],
            };
        }
    }
    return {
        winner: null,
        winningSquares: null,
    };
}

class Board extends React.Component {

    renderSquare(i) {
        return (
            <Square
                index={i}
                squares={this.props.squares}
                value={ this.props.squares[i] }
                onClick={ () => this.props.onClick(i) }
            />
        );
    }

    render() {
        const boardRows = 3;
        const boardColumns = 3;
        let index = null;

        return (
            <div className="game-board">
                {
                    [...Array(boardRows)].map(() => {
                        return (
                            <div className="board-row">
                                {
                                    [...Array(boardColumns)].map(() => {
                                        index === null ? index = 0 : index += 1;
                                        return this.renderSquare(index)
                                    })
                                }
                            </div>
                        )
                    })
                }
            </div>
        );
    }
}

class Game extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            history: [{ 
                squares: Array(9).fill(null),
                location: null,
            }],
            firstPlayerTurn: true,
            stepNumber: 0,
            ascending: true,
        };
    }

    reverseArray(array) {
        const output = [];
        for (var i = array.length - 1; i >= 0; i--) {
            output.push(array[i])
        }
        return output
    }

    handleClick(i) {
        const history = this.state.history.slice(0, this.state.stepNumber + 1);
        const current = history[history.length - 1];
        const squares = [...current.squares];
        const location = ['(1, 1)', '(2, 1)', '(3, 1)', '(1, 2)', '(2, 2)', '(3, 2)', '(1, 3)', '(2, 3)', '(3, 3)'];
        if (calculateWinner(squares).winner || squares[i]) {
            return;
        }
        squares[i] = this.state.firstPlayerTurn ? 'X' : 'O';
        this.setState({
            history: history.concat([{
                squares,
                location: location[i],
            }]),
            firstPlayerTurn: !this.state.firstPlayerTurn,
            stepNumber: history.length,
        });
    }

    jumpTo(step) {
        this.setState({
            stepNumber: step,
            firstPlayerTurn: (step% 2) === 0,
        });
    }

    toggleOrder() {       
        this.setState({ 
            ascending: !this.state.ascending,
        });
    }

    render() {
        const history = this.state.history;
        const current = history[this.state.stepNumber];
        const winner = calculateWinner(current.squares).winner;

        const moves = history.map((step, move) => {
            console.log(step);
            const desc = move ?
                `Go to move # ${move} ${step.location}` :
                'Go to game start';
            return (
                <li key={move}>
                    <button className="game-info-moves" onClick={() => this.jumpTo(move) }>
                        {move === this.state.stepNumber ? <b>{desc}</b> : desc}
                    </button>
                </li>
            );
        });

        let status;
        if (winner) {
            status = 'Winner: ' + winner;
        } else {
            status = `Next player: ${this.state.firstPlayerTurn ? 'X' : 'O'}`;
        }

        return (
        <div className="game">
            <div className="board-area">
            <Board 
                squares={current.squares}
                onClick={(i) => this.handleClick(i)}
            />
            </div>
            <div className="game-info">
            <div className="game-info-status">{ status }</div>
            <button className="game-info-sort" onClick={() => this.toggleOrder()}>{ this.state.ascending ? 'Descending' : 'Ascending' }</button>
            <CSSTransitionGroup 
                className="moves"
                component="ul"
                transitionName="moves"
                transitionEnterTimeout={500}
                transitionLeaveTimeout={150}
            >
                { this.state.ascending ? moves : this.reverseArray(moves) }
            </CSSTransitionGroup>
            </div>
        </div>
        );
    }
}

ReactDOM.render(
    <Game />,
    document.getElementById('root')
);
  


